import React from 'react';
import ReCAPTCHA from 'react-google-recaptcha';
import ErrorText from './ErrorText';

const Recaptcha = ({ updateUserDetail, error }) => (
  <>
    <ReCAPTCHA
      data-testid="recaptcha"
      size="normal"
      sitekey={process.env.REACT_APP_RECAPTCHA_KEY || process.env.REACT_APP_TEST_RECAPTCHA_KEY}
      onChange={(value) => updateUserDetail('recaptchaResponseToken', value)}
    />
    {error.isError && <ErrorText name="recaptcha" error={error} />}
  </>
);

export default Recaptcha;
